.about-paper:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: -webkit-gradient(linear, left top, right bottom, from(#2e6dd3), to(#9c6dd6));
  background-image: -webkit-linear-gradient(top left, #2e6dd3, #9c6dd6);
  background-image: -o-linear-gradient(top left, #2e6dd3, #9c6dd6);
  background-image: linear-gradient(to bottom right, #2e6dd3, #9c6dd6);
  opacity: 0.75;
}
.tab-paper{
  margin: 1rem;
  width: 90vw;
}
.landing-heading{
  color: #fff;
  font-family: Ubuntu;
  font-size: 3rem;
  z-index: 22;

}
.landing-text{
  color: #fff;
  font-family: Nanum Brush Script;
  font-size: 2rem;
  z-index: 22;

}
.flex-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}
.home-card{
  width: 350px;
  height: 280px;
  border: 1px solid #eee;
  border-radius: 1rem;
  -webkit-box-shadow: 4px  8px 13px #ccc;
          box-shadow: 4px  8px 13px #ccc;
  margin-bottom: 1rem;
  cursor: pointer;
  -webkit-transition: 0.3s all ease-in-out;
  -o-transition: 0.3s all ease-in-out;
  transition: 0.3s all ease-in-out;
}
.home-card:hover{
    -webkit-box-shadow: 6px  12px 15px #999;
            box-shadow: 6px  12px 15px #999;
}
.home-card>h3{
  color: #999;
}
.fa-apple{
  color: #8e8e93 !important;
}
.fa-android{
  color: #a4c639 !important;
}
.fa-react{
  color: #00d8ff !important;
}
.fa-angular{
  color: #b52e31 !important;
}
.fa-node-js{
  color: #6cc24a !important;
}
.fa-globe{
  color: #4e5860 !important;
}
.light-background{
  background: #f4f4f4;
}
.flex-image-container{
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
}
.flex-description{
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
}
.flex-description>h1{
  font-size: 2rem;
}
.flex-description>p{
  font-size: 1.2rem;
}
.flex-image{
  width: 100%;
}
.contact-background{
  padding: 4rem 1rem;
  position: relative;
  background: url('../../../assets/images/sunset.jpeg') fixed;
}
.contact-background:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: -webkit-gradient(linear, left top, right bottom, from(#396370), to(#0eaf67));
  background-image: -webkit-linear-gradient(top left, #396370, #0eaf67);
  background-image: -o-linear-gradient(top left, #396370, #0eaf67);
  background-image: linear-gradient(to bottom right, #396370, #0eaf67);
  opacity: 0.76;
}

.contact-input{
  width: 95%;
  border: none;
  padding: 1rem;
  border-radius: 1rem;
  background: rgba(190, 205, 229, 0.5);
  font-size: 1.6rem;
  color: #fff;
  outline: none;

  -webkit-transition: 0.2s all ease-in-out;

  -o-transition: 0.2s all ease-in-out;

  transition: 0.2s all ease-in-out;
}
.contact-input:focus{
  background: rgba(20, 56, 186, 0.8);
}
@media only screen and (max-width: 648px){
}



